








































































.description {
  border-top: 1px #eee solid;
  padding-top: 1em; }
.meta {
  margin-top: 1em;
  font-size: 0.9em;
  font-weight: bold;
  opacity: 0.8;
  letter-spacing: 0.01em; }
i {
  margin-right: 5px; }
.level-box {
  margin-left: 5px;
  padding: 0 5px;
  border: 1px #CCC solid;
  border-radius: 4px;
  display: inline-block; }
.box {
  padding: 1em 0;
  margin: 1em 0; }
.level, .levels {
  font-size: 0.86em; }
ul, li {
  padding: 0;
  margin: 0; }
ul {
  margin-top: 0.5em;
  padding-left: 1.25em; }
.isPublished.not {
  color: #e53c3c; }
