





























































































































































































































































































































































































































#prefect {
  .el-collapse-item__header {
    .sli-svg {
      transform: scale(0.8) translate(0px, 6px); } }

  .el-card__body {
    padding-top: 0 !important; }

  .userRow:first-child {
    border-top: 1px #EEE solid; }
  .userRow {
    border-bottom: 1px #EEE solid;
    .student {
      padding: 10px 0;
      .i {
        margin-right: 8px;
        width: 16px;
        height: 16px;
        transform: translate(0, 2px); } }
    .buttons {
      padding-top: 6px;
      button {
        float: right; } } }

  .header {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 0.75em;
    letter-spacing: 0.03em; }
  .subject {
    .el-collapse-item__header {
      font-size: 1em;
      font-weight: normal; } }

  .level, .levels {
    font-size: 0.86em; }
  .small {
    font-size: 0.85em; }
  .notesWrapper {
    margin-top: -10px; }
  ul, li {
    margin: 0;
    padding: 0; }
  .prefectNotes {
    margin-top: 2em; }
  ul.notes {
    margin-bottom: 2em;
    max-height: 12.3em;
    overflow-y: auto;
    padding: 1em 1em 1em 2em;
    margin: 1em 0 0 0;
    border: 1px #DDD solid;
    border-radius: 5px; }
  li.note {
    line-height: 1.6em;
    padding-bottom: 0.5em; }
  .projects {
    margin: 3px 0 0 3px; }
  .project {
    padding: 3px 0;
    svg {
      width: 16px;
      height: 16px;
      transform: translate(-4px, 2.5px); } }
  .selfEvals {
    margin-top: 1em; }
  .Chart {
    margin-top: 2em; }
  .removeNote {
    margin: 0;
    padding: 0;
    transform: translate(4px, 1px); } }
